body {
	overflow-x: hidden;
}

.wp-block-audio {
	margin: 0;
}

.wp-block-audio audio {
	width: 100%;
}

.wp-block-button {
	margin-bottom: 1.5em;
}

.wp-block-button__link {
	border: none;
	border-radius: 0;
	box-shadow: none;
	cursor: pointer;
	display: inline-block;
	font-size: 18px;
	line-height: 24px;
	margin: 0;
	padding: 11px 24px;
	text-align: center;
	text-decoration: none;
	white-space: normal;
	word-break: break-all;
}

.wp-block-columns {
	display: flex;
}

.wp-block-column {
	flex: 1;
}

.editor-inner-blocks {
	flex: 1;
}

.wp-block-cover-image {
	position: relative;
	background-size: cover;
	background-position: 50%;
	min-height: 430px;
	width: 100%;
	margin: 0 0 1.5em;
	display: flex;
	justify-content: center;
	align-items: center;
}

.wp-block-cover-image.has-parallax {
	background-attachment: fixed;
}

.wp-block-cover-image.has-background-dim::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.wp-block-cover-image.has-background-dim-10::before {
	background-color: rgba(0, 0, 0, 0.1);
}

.wp-block-cover-image.has-background-dim-20::before {
	background-color: rgba(0, 0, 0, 0.2);
}

.wp-block-cover-image.has-background-dim-30::before {
	background-color: rgba(0, 0, 0, 0.3);
}

.wp-block-cover-image.has-background-dim-40::before {
	background-color: rgba(0, 0, 0, 0.4);
}

.wp-block-cover-image.has-background-dim-50::before {
	background-color: rgba(0, 0, 0, 0.5);
}

.wp-block-cover-image.has-background-dim-60::before {
	background-color: rgba(0, 0, 0, 0.6);
}

.wp-block-cover-image.has-background-dim-70::before {
	background-color: rgba(0, 0, 0, 0.7);
}

.wp-block-cover-image.has-background-dim-80::before {
	background-color: rgba(0, 0, 0, 0.8);
}

.wp-block-cover-image.has-background-dim-90::before {
	background-color: rgba(0, 0, 0, 0.9);
}

.wp-block-cover-image.has-background-dim-100::before {
	background-color: black;
}

.wp-block-cover-image.has-left-content {
	justify-content: flex-start;
}

.wp-block-cover-image.has-right-content {
	justify-content: flex-end;
}

.wp-block-cover-image.alignleft, .wp-block-cover-image.alignright {
	max-width: 375px;
	width: 100%;
}

.wp-block-cover-image-text {
	color: #fff;
	font-size: 2em;
	line-height: 1.25;
	z-index: 1;
	margin-bottom: 0;
	max-width: 750px;
	padding: 14px;
	text-align: center;
}

.wp-block-cover-image-text a,
.wp-block-cover-image-text a:hover,
.wp-block-cover-image-text a:focus,
.wp-block-cover-image-text a:active {
	color: #fff;
}

.has-left-content .wp-block-cover-image-text {
	margin-left: 0;
	text-align: left;
}

.has-right-content .wp-block-cover-image-text {
	margin-right: 0;
	text-align: right;
}

.wp-block-embed figcaption {
	margin-top: 0.5em;
	text-align: center;
}

.wp-block-file {
	margin-bottom: 1.5em;
}

.wp-block-file.aligncenter {
	text-align: center;
}

.wp-block-file.alignright {
	text-align: right;
}

.wp-block-file__button {
	background: #000;
	color: #fff;
	padding: 0.5em 1em;
	text-decoration: none;
}

.wp-block-file__button:hover, .wp-block-file__button:visited, .wp-block-file__button:focus, .wp-block-file__button:active {
	color: #fff;
	opacity: 0.85;
	text-decoration: none;
}

* + .wp-block-file__button {
	margin-left: 0.75em;
}

.wp-block-gallery {
	display: flex;
	flex-wrap: wrap;
	list-style-type: none;
	padding: 0;
	margin: 0 -8px 0 -8px;
}

.wp-block-gallery.alignleft, .wp-block-gallery.alignright {
	max-width: 375px;
	width: 100%;
}

.blocks-gallery-item {
	margin: 8px;
	display: flex;
	flex-grow: 1;
	flex-direction: column;
	justify-content: center;
	position: relative;
}

.blocks-gallery-item figure {
	margin: 0;
	height: 100%;
}

@supports (position: sticky) {
	.blocks-gallery-item figure {
		display: flex;
		align-items: flex-end;
		justify-content: flex-start;
	}
}

.blocks-gallery-item img {
	display: block;
	max-width: 100%;
	height: auto;
	width: 100%;
}

@supports (position: sticky) {
	.blocks-gallery-item img {
		width: auto;
	}
}

.blocks-gallery-item figcaption {
	position: absolute;
	bottom: 0;
	width: 100%;
	max-height: 100%;
	padding: 40px 10px 5px;
	text-align: center;
	color: #fff;
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.7) 0, rgba(0, 0, 0, 0.3) 60%, transparent);
}

.blocks-gallery-item figcaption img {
	display: inline;
}

.is-cropped .blocks-gallery-item a,
.is-cropped .blocks-gallery-item img {
	flex: 1;
	width: 100%;
}

@supports (position: sticky) {
	.is-cropped .blocks-gallery-item a,
	.is-cropped .blocks-gallery-item img {
		flex: 1;
		height: 100%;
		object-fit: cover;
	}
}

.blocks-gallery-item {
	width: calc( 100% / 2 - 16px);
}

.columns-1 .blocks-gallery-item {
	width: calc( 100% / 1 - 16px);
}

@media (min-width: 600px) {
	.columns-3 .blocks-gallery-item {
		width: calc( 100% / 3 - 16px);
	}
	.columns-4 .blocks-gallery-item {
		width: calc( 100% / 4 - 16px);
	}
	.columns-5 .blocks-gallery-item {
		width: calc( 100% / 5 - 16px);
	}
	.columns-6 .blocks-gallery-item {
		width: calc( 100% / 6 - 16px);
	}
	.columns-7 .blocks-gallery-item {
		width: calc( 100% / 7 - 16px);
	}
	.columns-8 .blocks-gallery-item {
		width: calc( 100% / 8 - 16px);
	}
}

.wp-block-image {
	width: fit-content;
	max-width: 100%;
}

.wp-block-image img {
	max-width: 100%;
}

.wp-block-image.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	text-align: center;
}

.wp-block-image.is-resized {
	width: min-content;
	display: -ms-inline-grid;
	-ms-grid-columns: min-content;
}

.wp-block-image.is-resized figcaption {
	-ms-grid-row: 2;
}

.wp-block-image.is-resized img {
	max-width: none;
}

.wp-block-latest-posts.alignleft {
	margin-right: 2em;
}

.wp-block-latest-posts.alignright {
	margin-left: 2em;
}

.wp-block-latest-posts.is-grid {
	display: flex;
	flex-wrap: wrap;
	padding: 0;
	list-style: none;
}

.wp-block-latest-posts.is-grid li {
	margin: 0 16px 16px 0;
	width: 100%;
}

@media (min-width: 600px) {
	.wp-block-latest-posts.columns-2 li {
		width: calc( ( 100% / 2 ) - 16px);
	}
	.wp-block-latest-posts.columns-3 li {
		width: calc( ( 100% / 3 ) - 16px);
	}
	.wp-block-latest-posts.columns-4 li {
		width: calc( ( 100% / 4 ) - 16px);
	}
	.wp-block-latest-posts.columns-5 li {
		width: calc( ( 100% / 5 ) - 16px);
	}
	.wp-block-latest-posts.columns-6 li {
		width: calc( ( 100% / 6 ) - 16px);
	}
}

.wp-block-latest-posts__post-date {
	display: block;
}

.wp-block-pullquote {
	padding: 3em 0;
	text-align: center;
}

.wp-block-pullquote.alignleft, .wp-block-pullquote.alignright {
	max-width: 375px;
}

.wp-block-pullquote cite,
.wp-block-pullquote footer {
	position: relative;
}

.wp-block-subhead {
	font-size: 1.125em;
	font-style: italic;
	opacity: 0.75;
}

.screen-reader-text {
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	word-wrap: normal;
	border: none;
}

.screen-reader-text:active, .screen-reader-text:focus {
	clip: auto;
	clip-path: none;
	display: block;
	height: auto;
	width: auto;
	z-index: 100000;
	text-decoration: none;
}

@media (min-width: 375px) {
	.alignleft {
		float: left;
		margin: 0 0 1.5rem 1.5rem;
		max-width: 50%;
	}
}

@media (min-width: 375px) {
	.alignright {
		float: left;
		margin: 0 1.5rem 1.5rem 0;
		max-width: 50%;
	}
}

.aligncenter {
	display: block;
	margin-left: auto;
	margin-right: auto;
	clear: both;
}

.alignfull,
.alignwide {
	margin-left: calc( -100vw / 2 + 100% / 2);
	margin-right: calc( -100vw / 2 + 100% / 2);
	width: auto;
	max-width: 100vw;
}

@media (min-width: 750px) {
	.alignwide {
		margin-left: -25%;
		margin-right: -25%;
		max-width: calc( 750px + 50%);
	}
}

.has-charcoal-color {
	color: #282c34;
}

.has-charcoal-background-color {
	background-color: #282c34;
}

.has-regent-color {
	color: #8c97a7;
}

.has-regent-background-color {
	background-color: #8c97a7;
}

.has-husk-color {
	color: #b9a364;
}

.has-husk-background-color {
	background-color: #b9a364;
}

.has-red-stage-color {
	color: #b15330;
}

.has-red-stage-background-color {
	background-color: #b15330;
}

.has-small-font-size {
	font-size: 12px;
}

.has-regular-font-size {
	font-size: 16px;
}

.has-large-font-size {
	font-size: 36px;
}

.has-larger-font-size {
	font-size: 48px;
}



